<!-- 订单信息 -->
<template>
	<div class=" container d-flex justify-content-between">
			<div class="col-1">
				<span style="font-size: 13px;">订单中心</span>
			</div>
			
			<div class="col border" style="background-color: white;">
				<br>
					<div class="d-flex justify-content-between">
						<el-tabs v-model="activeName" @tab-click="switchover(activeName)" type="card">
							<el-tab-pane label="全部订单" name="allorder"></el-tab-pane>
							<el-tab-pane :label="'待付款'+pendingpaymentcount" name="pendingpayment"></el-tab-pane>
							<el-tab-pane :label="'待收货'+Pendinggoodscount" name="Pendinggoods"></el-tab-pane>
							<el-tab-pane :label="'待评价'+Pendingevaluatecount" name="Pendingevaluate"></el-tab-pane>
							<el-tab-pane :label="'已完成'" name="accomplish"></el-tab-pane>
						</el-tabs>
						<el-input placeholder="请输入内容" v-model="input" style="width: 350px;" class="input-with-select">
							<el-select style="width: 90px;" v-model="select" slot="prepend">
								<el-option label="订单号" value="1"></el-option>
								<el-option label="电话" value="2"></el-option>
							</el-select>
						<el-button slot="append" icon="el-icon-search"></el-button>
						</el-input>
					</div>
					<br>
					
					<table class="table col">
						<tr style="font-size: 12px;">
						<th>订单详情</th>
						<!-- <th>订单编号</th> -->
						<th>金额</th>
						<th>收货人</th>
						<th>联系电话</th>
						<th>{{activeName=="allorder"
						?"订单状态":activeName=="pendingpayment"
						?"等待付款":activeName=="Pendinggoods"
						?"等待收货":activeName=="Pendingevaluate"
						?"等待评价":activeName=="accomplish"
						?"是否完成交易":"订单状态"}}</th>
						<th>操作</th>
						</tr>
						<template v-for="(order,index) in orderLists">
							<span style="font-size: 12px; float: left;" class="mt-3" :key="order">
								订单编号：{{order.oredrnumber}}</span>
							<tr class="border ordertr" style="font-size: 12px;" :key="order">
								<td>
									<div class="d-flex justify-content-between">
									<div class="d-flex">
										<div @click="commoditydetailed(order.commodityid)" class="border d-flex align-items-center justify-content-center"
										style="width: 70px; height: 70px;cursor: pointer;">
											<img :src="require('@/assets/'+order.commoditypicture)" width="60px" >
										</div>	
										&nbsp;<div style="width: 200px;" 
										class="text-left">
												{{order.commodityname}}
										</div>
									</div>
									<div style="float: right;">X{{order.count}}</div>
									</div>
								</td>
								<td><span>￥{{order.totalprice}}</span></td>
								<td>{{order.username}}</td>
								<td>{{order.phone}}</td>
								<td>
									{{activeName=="allorder"
									?order.orderstate:activeName=="pendingpayment" 
									?(order.paymentstate==2?"等待商家确认":"未付款"):activeName=="Pendinggoods"
									?"未收货":activeName=="Pendingevaluate"
									?"未评论":activeName=="accomplish"
									?order.orderstate:"订单状态"}}
								</td>
								<td>
									<div class="d-flex justify-content-center">
										<template v-if="activeName=='allorder'||activeName=='accomplish'" >
											<el-button style="font-size: 12px;">订单详情</el-button>
										</template>
										
										<template v-if="activeName=='pendingpayment'&& dialogVisible!=''">
											<el-button type="text" :disabled="order.paymentstate==2" @click="dialogVisible[index].dialogVisibleString = true" >立即付款</el-button>
											<el-dialog
											title="付款码"
											:visible.sync="dialogVisible[index].dialogVisibleString"
											width="30%">
												<img src="../../assets/imgs/收款码.png"  class="col-8" >
												<div class="d-flex justify-content-between">
													<el-button type="danger" @click="dialogVisible[index].dialogVisibleString = false" round>取消支付</el-button>
													<el-button type="danger" @click="operate(activeName,order.id)" round>完成支付</el-button>
												</div>
											</el-dialog>
										</template>
										
										<template v-if="activeName=='Pendinggoods'">
											<el-button style="font-size: 12px;" @click="operate(activeName,order.id)">确认收货</el-button>
										</template>
										
										<template v-if="activeName=='Pendingevaluate' &&drawer!=''">
											<el-button style="font-size: 12px;" 
											@click="drawer[index].drawerString=true">立即评价</el-button>
										
											<!-- 商品评论模板 -->
												<el-drawer
													title="给你的宝贝进行评论吧!"
													:visible.sync="drawer[index].drawerString"
													direction="btt">
													
													<div class="container">
													<el-input 
												type="textarea"
													placeholder="请输入内容"
												v-model="textComment"
												maxlength="200"
												show-word-limit />
													</div>
													<div class=" mt-2 mb-2 d-flex justify-content-around container">
														<el-button type="danger" @click="drawer[index].drawerString=false" round>取消</el-button>
														<el-button type="danger" @click="addCommodityComment(order.commodityid,activeName,order.id)" round>确认</el-button>
													</div>
													</el-drawer>
										</template>
										<div style="width: 10px;" class="ml-2">
											<i class="el-icon-delete" @click="delorder(order.id)"></i>
										</div>
										
									</div>
								</td>
								</tr>
								</template>
					</table>
		</div>

</div>
</template>

<script>
	import {Ajax} from '../../assets/js/ajax.js'
	export default{
		name:"OrderInfo",
		data:function(){
			return{
				textComment:'',
				drawer:'',
				dialogVisible:'',
				activeName: 'allorder',
				select:'1',
				input:'',
				user:[],
				orderLists:[],
				pendingpaymentcount:0,
				Pendinggoodscount:0,
				Pendingevaluatecount:0,
			}
		},
		created:function(){
			//查询当前用户
			Ajax("get","user/homePageUser",null,(data)=>{
				this.user=data
					//查询当前用户的购物车
					var dataOredrList="uid="+parseInt(data.id);
					Ajax("post","orderList/getOrderListUid",dataOredrList,(dataOrder)=>{
						this.orderLists=dataOrder
						this.titlecount(dataOrder)
					})
			}) 
		},
		methods:{
		
			//切换订单
			switchover:function(title){
				var datastate="uid="+parseInt(this.user.id)+"&statetitle="+title;
				Ajax("post","orderList/getOrderListUid",datastate,(dataOrder)=>{
					this.orderLists=dataOrder
					this.dialogVisible=''
					this.drawer=''
					//查询未付款
					if(title=="pendingpayment"){
						this.pendingpaymentcount=0;
						for(let i=0;i<dataOrder.length;i++){
						if(dataOrder[i].paymentstate!=1){
							this.pendingpaymentcount++;
						}
					}
					//解决付款吗id覆盖问题
					this.dialogVisible=[]
					for(let i=0;i<this.pendingpaymentcount;i++){
						this.dialogVisible.push({"dialogVisibleString":false})
					}
				}
				
				//查询待收货
					if(title=="Pendinggoods"){
						this.Pendinggoodscount=0;
						for(let i=0;i<dataOrder.length;i++){
						if(dataOrder[i].freightstatus=="未收货" && dataOrder[i].paymentstate==1){
							this.Pendinggoodscount++;
						}
					}
				}
				//查询待评价
					if(title=="Pendingevaluate"){
						this.Pendingevaluatecount=0;
						for(let i=0;i<dataOrder.length;i++){
					if(dataOrder[i].commentstatus==0 &&
						dataOrder[i].freightstatus=="已收货" && dataOrder[i].paymentstate==1){
						this.Pendingevaluatecount++;
						}
					}
					//解决待评价id覆盖问题
					this.drawer=[]
					for(let i=0;i<this.Pendingevaluatecount;i++){
						this.drawer.push({"drawerString":false})
					}
				}
				//查询全部的数量
				if(title=="allorder"){
					this.titlecount(dataOrder)
				}
			})
		},
			//删除订单
			delorder:function(id){
				this.$confirm('此操作将永久删除订单, 是否继续',{
          confirmButtonText:'确定',
          cancelButtonText:'取消',
					type: 'warning'
        }).then(() => {
					var dataOredrListid="id="+parseInt(id);
					Ajax("post","orderList/delOredrListid",dataOredrListid,(dataOrder)=>{
						if(dataOrder.success==true){
						this.$message({
							type: 'success',
							message: dataOrder.message
						});
						this.switchover(this.activeName);
						}else{
							this.$message({
								type: 'success',
								message: dataOrder.message
							});
						}
					})
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
			},
			//进入商品详情
			commoditydetailed:function(productId){
				this.$router.push({
						name:'CommoditydetailedPage',
						params:{id:productId}
				})
			},
			//添加商品评论
			addCommodityComment:function(commodityid,activeName,id){
				alert(commodityid);
				var data="commentmessage="+this.textComment+"&userid="+this.user.id+"&commodityid="+commodityid;
				Ajax("post","commoditycomment/addCommodityComment",data,(result)=>{
					if(result.success==true){
						this.textComment=''; //评论输入框清空
						this.operate(activeName,id)//调用订单操作方法	
					}else{
						alert(result.message)
					}
				})
			},
			//操作
			operate:function(title,id){
				var dataUpdate="id="+parseInt(id)+"&statetitle="+title;
				Ajax("post","orderList/updateOrderList",dataUpdate,(dataOrder)=>{
					if(dataOrder.succes==true){
						if(title=="pendingpayment"){
							alert(dataOrder.message)
						}
					}else{
						alert(dataOrder.message)
					}
				this.switchover(title)	
				//再次查询条数
				var datastate="uid="+parseInt(this.user.id)+"&statetitle=allorder";
				Ajax("post","orderList/getOrderListUid",datastate,(dataOrderAll)=>{
					this.titlecount(dataOrderAll);
				})
				
				})
			},
			//查询商品各个标题的数量
			titlecount(orders){
				this.pendingpaymentcount=0;
				this.Pendinggoodscount=0;
				this.Pendingevaluatecount=0;
				for(let i=0;i<orders.length;i++){
					//查询未付款的商品数量
					if(orders[i].paymentstate!=1){
						this.pendingpaymentcount++;
					}
					//查询未收货的商品数量
					if(orders[i].freightstatus=="未收货" && orders[i].paymentstate==1){
						this.Pendinggoodscount++;
					}
					//查询未评价的商品数量
					if(orders[i].commentstatus==0 && 
					orders[i].freightstatus=="已收货" && orders[i].paymentstate==1){
						this.Pendingevaluatecount++;
					}
				}
			}
		}
	}
</script>

<style>
	.ordertr .el-icon-delete{
		display: none;
		cursor: pointer; 
		font-size: 18px;
	}
	.ordertr:hover .el-icon-delete{
		display: block;
	}
	.ordertr .el-button{
		/* display: flex;
		justify-content: center; */
		/* width: 60px; */
	}
	.el-message-box__btns{
		display: flex;
		justify-content: flex-end;
	}
</style>
